/* App.css */

/* Reset default styles */
body,
h1,
h2,
h3,
p,
ul {
  margin: 0;
  padding: 0;
}

.modal-content {
  background-color: #42a0568a;
}

.modal-content button {
  margin: 10px;
}

.shopping-cart-summary {
  margin: 10px;
  color: wheat;
}

.card2 p {
  color: white;
  margin: 10px;
}

.caaaaard {
  background-color: transparent;
  margin-top: 40px;
}

.user-details {
  background-color: #42424278;
  width: 300px;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  border: 1px solid #ddd;
  margin: 10% 40%;
}

.user-details p {
  margin: 10px;
  font-size: large;
}

.user-details h3 {
  margin: 10px;
  font-size: large;
}

.userEd-Cont {
  background-color: #42424278;
  width: 300px;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  border: 1px solid #ddd;
  margin: 10% 35%;
}

.input-user label {
  color: #000;
  font-size: large;
}

.userEd-Cont h2 {
  margin-bottom: 20px;
}

.input-user button {
  margin-top: 20px;
  border-color: transparent;
  height: 35px;
  font-size: 20px;
}

.input-user input {
  margin-left: 22%;
  margin-top: 5px;
  display: flex;
}

.error-container {
  background-color: #9b00004a;
  width: 100%;
  height: 200px;
}

.error-container p1 {
  font-size: 30px;
  color: #7e000d;
}

a {
  text-decoration: none;
}

body {
  font-family: Arial, sans-serif;
}

/* Common styles */
.container-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Header styles */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
}

.questions {
  margin-top: 200px;
  background-color: #424242;
  height: 100px;
  width: 100%;
  position: sticky;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: right;
  align-items: center;
}

.starving-section {
  justify-content: center;
  background-color: #424242ad;
  display: flex;
  height: 150px;
  position: relative;
  width: 100%;
  margin-top: 30px;
}

.questions button {
  background-color: #c4c4c4;
  font-size: 20px;
  border-color: transparent;
  border-radius: 10px;
  margin-right: 25px;
}

.starving-section h1 {
  color: #ffffff;
  margin-top: 50px;
}

.starving-section .search-bar-container input {
  margin-top: 55px;
  margin-left: 50px;
  height: 25px;
  background-color: #9b0000;
  color: #ffffff;
  font-size: large;
  border-color: transparent;
}

.header .cart {
  font-size: 15px;
  background-color: #ffffffcc;
  display: flex;
  align-items: center;
}

.header .cart img {
  margin-right: 5px;
  width: auto;
  height: 30px;
}

.popopo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .ll {
  background-color: #cccccc3a;
}

.popopo button {
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
}

.cart {
  margin-right: 400px;
}

.admin {
  background-color: transparent;
}

.back {
  background-color: #7575759d;
  font-size: 25px;
  border-color: #f0bf11;
  border-radius: 2%;
  margin-left: 100px;
  color: #a10212;
  padding: 5px;
}

.button-login img {
  margin-right: 5px;
  width: auto;
  height: 45px;
}

.button-login {
  font-size: 20px;
  color: #f0bf11;
  box-shadow: 0 0px 18px rgba(250, 226, 5, 0.784);
  background-color: transparent;
  border-color: transparent;
  display: flex;
  align-items: center;
}

.start-head {
  margin: 0 1rem;
}

.button {
  padding: 0.5rem 1rem;
  margin-left: 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
}

.cart {
  background-color: #eee;
}

/* Content styles */
.main-content {
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  padding: 1rem;
}

/* Card styles */
/* .product-card,
.shopping-cart,
.login-window, */
.register-window {
  width: 300px;
  padding: 2rem;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  border: 1px solid #ddd;
  margin: 10% 35%;
}

/* Form input styles */
.product-card input[type="text"],
.product-card input[type="password"],
.register-window input {
  width: 80%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
}

/* Button styles */
.product-card button,
.modal-content button,
.login-window button,
.register-window button {
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.product-card .submit-button {
  background-color: #424242;
  color: white;
}

.register-window .register-button {
  background-color: #424242;
  color: white;
}

.product-card .back-button,
.register-window .back-button {
  background-color: #424242;
  color: #007bff;
}

/* Button hover effects */
.product-card .submit-button:hover {
  background-color: #0056b3;
}

.register-window .register-button:hover {
  background-color: #218838;
}

.product-card .back-button:hover,
.register-window .back-button:hover {
  text-decoration: underline;
}

/* Specific styles */
.shopping-cart-container {
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.5);
}

.shopping-cart {
  width: 400px;
}

.close-button {
  background-color: #dc3545;
  color: white;
}

.close-button:hover {
  background-color: #c82333;
}

/* Styles for the LoginPage component */
.login-container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: flex-start; /* Aligns children to the start of the container's main axis */
  align-items: center;
  margin: 0; /* Resets any margin */
  position: relative;
}

.login-window {
  width: 350px;
  padding: 2rem;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin: 0 80%;
}

.login-window h2 {
  margin-bottom: 1.5rem;
  color: #333; /* Text color */
}

.login-window input[type="text"],
.login-window input[type="password"] {
  width: 80%;
  padding: 0.8rem;
  margin-bottom: 1.5rem;
  border: 1px solid #ccc; /* Input border color */
  border-radius: 4px;
  background-color: #f9f9f9; /* Input background color */
  color: #333; /* Input text color */
}

.login-window .register-button a {
  color: #333;
}

.submit-button {
  padding: 0.8rem 1rem;
  border: none;
  border-radius: 4px;
  background-color: #424242; /* Google Blue */
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Smooth transition */
}

.submit-button:hover {
  background-color: #2a65f7; /* Darker shade on hover */
}

.back-button {
  background-color: transparent;
  color: #424242; /* Google Blue */
  border: 1px solid #4285f4; /* Border matching button color */
  padding: 0.8rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Smooth transition */
}

.back-button:hover {
  background-color: rgba(66, 133, 244, 0.1); /* Light background on hover */
}

/* Additional styles for checkbox */
label {
  display: block;
  margin-bottom: 1rem;
  color: #666; /* Checkbox text color */
}

/* ... (existing styles) ... */

body {
  font-family: Arial, sans-serif;
  text-align: center;
  margin: 0;
  padding: 0;
  background-image: url("/src/images/BackGr.jpg"); /* Set the background image */
  background-size: cover; /* Ensure the background image covers the entire area */
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh; /* Set the height of the container to full viewport height */
  position: relative; /* Enable positioning for child elements */
}

/* Styles for the logo image */
.logo {
  position: absolute;
  left: 300px; /* Adjust the distance from the left */
  transform: translateX(-50%); /* Center the logo horizontally */
}

.logo img {
  width: 154px; /* Adjust the width of the image */
  height: auto; /* Maintain aspect ratio */
}

.product-card {
  width: 250px;
  padding: 1rem;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  border: 1px solid #ddd;
  margin: 1rem; /* Margin between cards */
}

.product-card img {
  width: 100%; /* Make the image fill the card */
  border-radius: 8px; /* Ensure rounded corners for the image */
}

.shopping-cart img {
  max-width: 100%; /* Set maximum width to 100% */
  height: auto; /* Maintain aspect ratio */
}

.admin-window h2 {
  margin-left: 100px;
}

.close-button {
  margin-top: 20px;
  margin-left: 100px;
  font-size: 25px;
}

.edit-user-button {
  background-color: #9b0000;
  font-size: large;
}

.products-list {
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 50px;
  display: flex;
  justify-content: flex-start; /* Aligns children to the start of the container's main axis */
  align-items: center;
  gap: 10px;
  background-color: #424242;
  box-shadow: 0 0px 18px rgba(250, 226, 5, 0.784);
}

.products-list h3 {
  margin: 25px;
  font-size: 25px;
  color: #f0bf11;
}

.products-list img {
  height: 150px;
  width: auto;
}

.products-list p {
  margin: 25px;
  font-size: 25px;
  color: white;
}

.products-list button {
  margin: 25px;
  font-size: 25px;
  background-color: #8c8b8b;
  color: white;
  background-color: #218838;
}

li {
  list-style: none;
}
.profilePhoto {
  width: 100px;
  height: 100px;
}

@media only screen and (max-width: 600px) {
  .header {
    flex-direction: column;
    align-items: center;
  }
  .questions {
    height: 5vh;
  }
  .starving-section,
  .questions {
    font-size: 4vw;
  }
  .products-list {
    flex-direction: column;
    align-items: center;
  }
  .product-card {
    width: 90%;
    margin: 5px;
    padding: 10px;
  }
  .product-card img {
    max-width: 100%;
    height: auto;
  }
  .product-card button,
  .product-card input[type="text"] {
    padding: 10px;
    font-size: 16px;
  }
  .products-list img {
    width: 70%;
    height: auto;
  }
  .products-list .prod-desc {
    display: none;
  }
  .products-list button {
    width: 50%;
    height: auto;
  }
  .starve {
    display: none;
  }
  .search-bar-container {
    width: 70%;
  }
  .search-bar-container input {
    width: 90%;
  }
  .logo {
    display: none;
  }
}

@media only screen and (min-width: 601px) and (max-width: 1024px) {
  .header {
    padding: 10px;
  }
  .starving-section,
  .questions {
    font-size: medium;
  }
  .products-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
  .products-list img {
    width: 70%;
    height: auto;
  }
  .product-card {
    width: 80%;
  }
  .logo {
    position: relative;
    top: 0;
    left: 0;
  }
}

@media only screen and (min-width: 1025px) {
  .header {
    padding: 20px;
  }

  .product-card {
    width: 70%;
  }
}
.button-cart {
  background-color: transparent !important;
}

@media only screen and (max-width: 600px) {
  .login-window {
    margin: 0 5%;
    width: 80%;
    height: auto;
  }
  .user-details {
    margin: 0 5%;
    width: 50%;
    height: auto;
    gap: 10px;
  }
}
@media only screen and (min-width: 601px) and (max-width: 1024px) {
  .login-window {
    margin: 10% 10%;
  }
  .user-details {
    margin: 0 5%;
    width: 50%;
    height: auto;
    gap: 10px;
  }
}
@media only screen and (max-width: 600px) {
  .register-window {
    margin: 10% 5%;
    width: 80%;
    height: auto;
  }
}
@media only screen and (min-width: 601px) and (max-width: 1024px) {
  .register-window {
    margin: 10% 10%;
  }
}
